import { render } from './renderer';

export default (() => {
    const launch = function (formData) {
        try {
            const initialResults = JSON.parse(
                    (_REST.response && _REST.response.body) || {} // eslint-disable-line
                ),
                queryStr = '%globals_server_QUERY_STRING^urldecode^striphtml%',
                vars = queryStr.split('&'),
                params = vars.reduce((p, pair) => {
                    let parts = pair.split('=');
                    if (parts.length === 2) {
                        p[decodeURIComponent(parts[0])] = decodeURIComponent(
                            parts[1]
                        );
                    }
                    return p;
                }, {});
            /* eslint-disable*/
            render(formData, config, params, initialResults, (err, html) => {
                //config coming from squiz global-config.js
                print(err ? 'err: ' + err : html);
            });
            /* eslint-enable*/
        } catch (e) {
            print('e:' + e.name + ':' + e.message + ' stack: ' + e.stack);
        }
    };

    return {
        launch: launch,
    };
})();
