import React from 'react';
import { renderToString } from 'react-dom/server';

export const render = (formData, config, initialQuery, initialResults, cb) => {
    const Form = formData,
        ServerRenderer = (
            <Form
                config={config}
                initialQuery={initialQuery}
                initialResults={initialResults}
            />
        ),
        formHtml = renderToString(ServerRenderer);

    cb(null, formHtml);
};
